

























import _ from 'lodash';
import { Component, Vue, Watch } from 'vue-property-decorator'
// import {Table,FilterItem,Pagination,Dialog} from '@/components/views/oss/img/manage'
import { Table,FilterItem,Pagination,Dialog } from '@/pages/adminWeb/components/views/oss/image'
import axios from 'axios'
@Component({
  // components:{Table,FilterItem,Pagination,Dialog}
  components: { Table,FilterItem,Pagination,Dialog }
})
export default class ViewComponent extends Vue {
  /* data */
  direct = 'api/image' // 
  // 
  propTable = {
    tableData: [],
    direct: this.direct, 
  }
  //- 
  propFilterItem = {
    ruleForm: {
      name: '',
      collected__date__gte: '',
      collected__date__lte: '',
    },
    active: false,
    direct: this.direct, //  
  }
  //- 
  propPagination = {
    pagination: {
      page_index: 1, // 
      total: 0, // 
      page_size: 5, // 
      page_sizes: [5, 10, 15, 20], // 
      layout: 'total,sizes,prev,pager,next,jumper', // 
    }
  }

  //- 
  isShowDialog = false
  propDialog = {}

  /* lifecycle hook */
  created() {
    // 
    this.requestApi() // 
  }

  /* method */
  // :
  emitTable(response: any) {
    // 
    if (response.method == 'delete') {
      this.requestApi(response);
      console.log('response',response)
    }
    // 
    if (response.method == 'patch') {
      if (response.mode == 'editInline') { // 
        delete response.mode
        this.requestApi(response);
        return;
      }

      this.propDialog = response
      this.isShowDialog = true
    }
  }
  // 
  emitFilterItem(response: any) {
    this.propPagination.pagination.page_index = 1; // 
    // 
    if (response.method === 'get') {
      const data = response.data;
      this.requestApi({
        data
      });
    }
    // 
    // &
    if (response.method === 'post') {
      this.propDialog = response
      this.isShowDialog = true
    }
  }
  // ,
  emitPagination(response: any) {
    this.requestApi()
  }
  // &&
  emitDialog(response: any) {
    // 
    const data = _.cloneDeep(response);
    data.edit = false;
    delete data.uid;
    delete data.status;
    // 
    const cloneTableData:any = _.cloneDeep(this.propTable.tableData);
    cloneTableData.unshift(data);
    this.propTable.tableData = [];
    setTimeout(()=>{
      this.propTable.tableData = cloneTableData;
    });

    // (this.propTable.tableData as any).unshift(response)

    // if (response.method === 'post') { // 
    //   this.propPagination.pagination.page_index = 1; // 
    //   this.propFilterItem.active = false // ,
    // }

    // this.requestApi(response)
  }

  async requestApi(conf: any = {}) {
    const {
      data = {},
      method = 'get',
      index = 0,
    } = conf
    const id = data._id || conf.id || '';
    const idVal = id ? '/' + id : '';
    Object.assign(data, this.propPagination) // 
    if (this.propFilterItem.active === true) {
      data.filterFields = this.propFilterItem.ruleForm // 
    }
    // console.log('data',data)

    const response = await this.$request({
      direct: this.direct + idVal,
      data,
      method,
    })
    // console.log('response:',response)

    
    
    if (response.pagination) {
      this.propPagination.pagination.total = response.pagination.total
    }

    if (method === 'post') { // 
      // const index = this.fileList.length-1;
      // const newChild:any = this.fileList[index];
      // newChild['_id'] = response._id;
      // this.fileList[index] = newChild;
    }
    else if (method == 'delete') { //  *
      const cloneTableData:any = _.cloneDeep(this.propTable.tableData);
      cloneTableData.splice(index, 1);
      this.propTable.tableData = []; // 
      setTimeout(()=>{
        this.propTable.tableData = cloneTableData
      })
      // this.propTable.tableData.splice(index, 1); // 


      this.propPagination.pagination.total--; // 
      this.$message({
        message: '',
        type: 'success'
      })
    }
    else if(method=='patch'){ // 
      this.$message({
        message:'',
        type: 'success'
      });

      // const cloneTableData:any = _.cloneDeep(this.propTable.tableData);
      // cloneTableData[index] = response
      // cloneTableData[index].edit = false
      // this.propTable.tableData = []; // 
      // setTimeout(()=>{
      //   this.propTable.tableData = cloneTableData
      // })

      // url
      const cloneTableData:any = _.cloneDeep(this.propTable.tableData);
      cloneTableData[index] = response
      cloneTableData[index].edit = false
      this.propTable.tableData = cloneTableData
    }
    else if (method == 'get') { // 
      response.results.forEach((item: any) => {
        // 
        item.edit = false
      })
      this.propTable.tableData = []; // 
      setTimeout(()=>{
        this.propTable.tableData = response.results
      })
    }
    
  }
}
